import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			oldPwdShow: true,
			newPwdShow: true,
			newPwdConfirmShow: true
		};
	},
	computed: {
		...mapState(['token'])
	},
	methods: {
...mapMutations(['logout', 'set_role', 'set_token']),
		changeEyeState(state) {
			var that = this;
			switch (state) {
				case 'oldPwd':
					that.oldPwdShow = !that.oldPwdShow
					break;
				case 'newPwd':
					that.newPwdShow = !that.newPwdShow
					break;
				case 'newPwdConfirm':
					that.newPwdConfirmShow = !that.newPwdConfirmShow
					break;
				default:
					break;
			}

		},
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack();
		},
		confirmSubmit() {
			const {
				oldPwd,
				newPwd,
				newPwdConfirm
			} = this;

			var now_token = this.token
			var that = this;
			if (oldPwd || newPwd || newPwdConfirm) {
				if (newPwd || newPwdConfirm) {
					if (oldPwd == '' || newPwd == '' || newPwdConfirm == '') {
						uni.showModal({
							showCancel: false,
							title: '提示',
							content: '输入的密码不能为空！',
							confirmText: '重新输入',
							success: function(res) {
								if (res.confirm) {

								}
							}
						});
					} else {
						if (newPwd === newPwdConfirm) {
							uni.showModal({
								title: '提示',
								content: '是否确认修改？',
								confirmText: '确定',
								cancelText: '取消',
								success: function(res) {
									if (res.confirm) {
										uni.showLoading({
											title: '正在提交...'
										})
										uni.request({
											url: that.global_baseUrl + '/set_pwd/',
											header: {
												'Authorization': ' Token ' + now_token,
												'content-type': 'application/x-www-form-urlencoded'
											},
											method: "POST",
											data: {
												oldPwd: that.oldPwd,
												newPwd: that.newPwd,
												newPwdConfirm: that.newPwdConfirm
											},
											success: (res) => {
												that.check_statusCode(res.statusCode);
												if(res.data.state==='ok'){
													setTimeout(() => {
														uni.hideLoading();
														uni.showToast({
															title: '密码修改成功！请重新登录！',
															icon: 'success'
														})
													}, 600);
													uni.clearStorageSync('local_token');
													that.set_token('')
													that.logout();
													that.set_role(2);
													if(that.token===''){
														uni.redirectTo({
															url: '/pages/public/login'
														})
													}
												}else{
													setTimeout(() => {
														uni.hideLoading();
														uni.showToast({
																title: '修改失败！请检查原密码！',
																icon: 'none'
															})
													}, 600);
												}
												
											}
										});

									} else {

									}
								}
							});

						} else {
							uni.showModal({
								showCancel: false,
								title: '提示',
								content: '两次新密码不一致！',
								confirmText: '重新输入',
								success: function(res) {
									if (res.confirm) {

									}
								}
							});
						}

					}
				} else {
					uni.showModal({
						showCancel: false,
						title: '提示',
						content: '输入的密码不能为空！',
						confirmText: '重新输入',
						success: function(res) {
							if (res.confirm) {

							}
						}
					});
				}
			} else {
				uni.showModal({
					showCancel: false,
					title: '提示',
					content: '输入的密码不能为空！',
					confirmText: '重新输入',
					success: function(res) {
						if (res.confirm) {

						}
					}
				});
			}
		},
	}
}
